html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Toolbar {
  height: 100%;
}

.Toolbar > div {
  text-align: center;
  color: #c1cfdd;
  line-height: 40px;
  width: 40px;
}

.Toolbar > div:hover {
  cursor: pointer;
  background-color: #f8f8f8;
}

.add {
  -webkit-text-stroke: 2px white;
  float: right;
}

.Editor {
  font-size: 18px;
  resize: none;
  border: none;
  padding: 40px;
  box-sizing: border-box;
  width: 100%;
  height: calc(100% - 3px);
}

textarea:focus{
  outline: none;
}

.Note {
  height: 70px;
  border-bottom: 1px solid;
  border-color: #f0f0f0;
}

.Note:hover {
  background-color: #f8f8f8;
  cursor: pointer;
}

.NoteTitle {
  text-align: left;
  padding: 20px 20px;
}

.selectedNote {
  background-color: #eef3f8 !important;
}
.NotesList {
}
.App {
  height: 100%;
  display: grid;
  grid-template-columns: 25% auto;
  grid-template-rows: 40px auto;
}

.ToolbarGrid {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  border-right: 1px solid #f0f0f0;
}

.NotesListGrid {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  border-right: 1px solid #f0f0f0;
  border-top: 1px solid #f0f0f0;
}

.EditorGrid {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 3;
  /* border-top: 1px solid #f0f0f0; */
}

